@media screen and (min-width:768px) {
  .app_header {
    background-color: mediumslateblue;
    height: 5vw;
  }
}

/*헤더*/
.app_header {
  width: 100%;
  height: 10vw;
  padding: 5% 5% 0 5%;
  box-sizing: border-box;
  background-color: #FEF9A7;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.app_header>h1 {
  margin: 0;
  font-size: 3vw;
  font-style: italic;
  color: black;
}

.app_header>nav {
  width: 40%;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 2.5vw;
  color: #343A40;
  cursor: pointer;
}

.app_header>nav>div:hover {
  color: #343a40bf;
}

section {
  width: 100%;
}

h2 {
  display: none;
}

/*섹션1*/
.sec_intro {
  background-color: #FEF9A7;
  height: 60vw;
  padding: 12% 0 0 0;
  box-sizing: border-box;
  text-align: center;
}

.sec_intro>p {
  /* background-color: darksalmon; */
  margin: 0;
  width: 100%;
  height: 100%;
  line-height: 10vw;
  font-size: 3.8vw;
  color: #343A40;
  letter-spacing: 10px;
  font-style: italic;
  overflow: hidden;
}

.sec_intro>p>span {
  /* background-color: orange; */
  display: block;
}

.sec_intro>p>span:nth-child(1) {
  animation: textUp 2.5s 0.2s ease-in-out;
}

.sec_intro>p>span:nth-child(2) {
  animation: textUp 2.5s ease-in-out;
}

.sec_intro>p>span:nth-child(3) {
  animation: textUp 2.5s 0.2s ease-in-out;
}


@keyframes textUp {
  0% {
    margin: 10% 0 0 0;
    opacity: 0;
    text-shadow: 2px 2px 10px #343a4043;

  }

  75% {
    margin: -1% 0 0 0;
    text-shadow: 1px 1px 5px #343a4043;
  }

  100% {
    margin: 0;
    opacity: 1;
  }
}

/*섹션2*/
.sec_projects {
  background-color: #fff;
  height: 80vw;
  padding: 0 0.5%;
  box-sizing: border-box;
}

h3 {
  /* background-color: mediumslateblue; */
  margin: 0;
  width: 100%;
  height: 20%;
  padding: 8% 0 0 0;
  box-sizing: border-box;
  text-align: center;
  font-size: 4.5vw;
  font-style: italic;
  letter-spacing: 1px;
  color: #343A40;
}

.underline {
  position: relative;
}

.underline::before {
  content: '';
  position: absolute;
  margin: 8px 0 0 0;
  top: 1.5vw;
  left: -3px;
  width: 105%;
  height: 1.5vw;
  background-color: #FFCC29;
  transform: skewX(-50deg);
  opacity: 0.5;
  z-index: -1;
}
.sec_projects>h3>.underline{
  z-index: 0;
}

.cover_projects {
  /* background-color: rosybrown; */
  margin: 10% 0 0 0;
  width: 100%;
  height: 40%;
  padding: 0 5%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.cover_content {
  /* background-color: rgba(25, 25, 112, 0.533); */
  margin: auto;
  width: 30%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  border: 1px solid #343A40;
  box-sizing: border-box;
  /* box-shadow: 3px 3px 5px 1px #343A40dd; */
  cursor: pointer;
}

.cover_content>img {
  width: 100%;
  display: block;
}

/*섹션3*/
.sec_skills {
  background-color: #fefaa73b;
  height: 80vw;
  padding: 0 0.5%;
  box-sizing: border-box;
}

.sec_skills>h3 {
  padding: 5% 0 0 0;
}

.cover_skills {
  /* background-color: #fff; */
  width: 100%;
  height: 75%;
  padding: 0 3%;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.cover_skillsContent {
  /* margin: 0 2% 0 0; */
  width: 33%;
  height: 30%;
  display: flex;
  flex-direction: column;
  position: relative;
  transition: 0.8s;
  transform-style: preserve-3d;
  cursor: pointer;
}

.front {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  backface-visibility: hidden;
}

.front>img {
  width: 60%;
  object-fit: cover;
}

.cover_skillsContent:nth-child(8)>.front>img {
  width: 45%;
}

.back {
  backface-visibility: hidden;
  transform: rotateY(180deg);
}

.back>div {
  /* background-color: aliceblue; */
  position: absolute;
  backface-visibility: hidden;
  width: 100%;
  height: 100%;
  padding: 0 5%;
  box-sizing: border-box;
  z-index: 1;
  top: 0;
  left: 0;
  display: flex;
  object-fit: cover;
  font-size: 2.3vw;
  color: #343A40;
}

.active {
  transform: rotateY(180deg);
}

/*섹션4*/
.sec_about {
  /* background-color: aliceblue; */
  height: 80vw;
  padding: 0 0.5%;
  box-sizing: border-box;
}

.cover_about {
  margin: 5% 0 0 0;
  width: 100%;
  height: 60%;
  padding: 0 5%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.cover_aboutImg {
  /* background-color: orange; */
  width: 35%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cover_about>div>img {
  width: 80%;
}

.cover_aboutText {
  background-color: oldlace;
  margin: 0;
  width: 65%;
  padding: 5%;
  box-sizing: border-box;
}

.cover_aboutText>p {
  background-color: #ccc;
  margin: 0;
}

/*섹션5*/
.sec_contact {
  background-color: #343A40;
  height: 40vw;
  padding: 0 0.5%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cover_contact {
  /* background-color: cornsilk; */
  width: 100%;
  height: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.cover_contact>div {
  width: 30%;
  height: 90%;
  padding: 10% 2%;
  border-top: 2px solid #ccc;
  border-bottom: 2px solid #ccc;
  box-sizing: border-box;
  white-space: pre-wrap;
  word-break: break-all;
  font-size: 1.8vw;
  color: #fff;
  letter-spacing: 1px;
}

.title_contact {
  /* background-color: #fff; */
  margin: 0 0 15% 0;
  display: inline-block;
  font-size: 2vw;
}

.cover_contact>div:nth-child(3)>a {
  color: #fff;
  text-decoration: none;
  font-style: italic;
}

/*풋터*/
.app_footer {
  background-color: #343A40;
  width: 100%;
  height: 5vw;
  text-align: center;
  font-size: 1.5vw;
  line-height: 5vw;
  color: #fff;
}