/*modal*/
@keyframes showModal {
    0% {
      margin: 2% 0 0 0;
      opacity: 0;
    }
    100%{
      margin: 0;
      opacity: 1;
    }
  }

.cover_modal {
    background-color: #fff;
    width: 100%;
    height: 80vw;
    position: absolute;
    z-index: 1;
    padding: 5% 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    animation: showModal 0.3s ease-in-out;
    color: #343A40;
  }
  .close_modal{
    /* background-color: #FEF9A7; */
    margin: 0 0 0 0;
    height: 15%;
    padding: 0 0 0 90%;
    box-sizing: border-box;
  }
  .close_modal>div{
    background: url(../public/cross_032.png) no-repeat 0 0; 
    width: 32px;
    height: 32px;
    cursor: pointer;
  }
  .cover_modal_content{
    /* background-color: thistle; */
    height: 80%;
    padding: 0 5%;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .cover_modal_content>div{
    /* background-color: #fff; */
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .cover_modal_content>div>img{
    width: 100%;
    height: 100%;
  }
  .cover_modal_content>ul{
    /* background-color: teal; */
    margin: 0;
    padding: 0;
    width: 55%;
  }
  .cover_modal_content>ul>li{
    margin: 0;
    list-style: none;
    font-size: 2.3vw;
    letter-spacing: 1px;
  }
  .cover_modal_content>ul>li:not(:nth-child(1)){
    margin: 3% 0;
  }
  .cover_modal_content>ul>li:last-child{
    margin: 5% 0 0 0;
  }
  .title{
    font-size: 2.7vw;
    letter-spacing: 1px;
  }
  .cover_modal_content>ul>li>a{
    background-color: #FEF9A7;
    margin: 0 5% 0 0;
    width: 30%;
    box-sizing: border-box;
    color: black;
    text-decoration: none;
    text-align: center;
    font-size: 2vw;
    letter-spacing: 1px;
    border: 1px solid #FEF9A7;
    border-radius: 10px;
    box-sizing: border-box;
    display: inline-block;
  }
  
  
  